<style scoped>
.notice {
    text-align: left;
    margin: 10px;
    padding: 10px;
    font-size: 14px;
}

.block-box {
    border-radius: 10px;
    background-color: #eee;
    padding: 10px;
    margin-bottom: 10px;
}

.down-url {
    margin-top: 5px;
    font-size: 1.1em;
}
</style>
<template>
<div>
    <sub-title title="" />
    <Row v-if="userType === '1'" class="block-box">
        <Col span="12">
        <Upload :before-upload="beforeQuesUp" :on-format-error="fileFormatError" ref="upload1" name="upfile" :format="['xls','xlsx']" :headers="headers" :with-credentials="true" action="http://localhost:8180/admin/question/importQuestionFromExcel">
            <Button icon="ios-cloud-upload-outline"></Button>
        </Upload>
        <div class="notice">
            <p>1.Excel</p>
            <p>2.</p>
            <p>3.</p>
        </div>
        </Col>
        <Col span="12">
        <Upload :before-upload="beforeStusUp" :on-format-error="fileFormatError" :with-credentials="true" ref="upload2" name="upfile" :format="['xls','xlsx']" :headers="headers" action="http://localhost:8180/admin/manage/importUserFromExcel">
            <Button icon="ios-cloud-upload-outline"></Button>
        </Upload>
        <div class="notice">
            <p>1.Excel</p>
            <p>2.</p>
            <p>3.</p>
        </div>
        </Col>
    </Row>

    <Row v-if="userType === '1'" class="block-box">
        <Col span="24">
        <h3 style="margin:8px;"></h3>
        <Select placeholder="" v-model="selectCate" style="width:200px">
            <Option v-for="item in tableCategoryList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <Button @click="downloadQues" style="margin-left:8px;"></Button>
        <div class="down-url" v-if="questionPrepared">
            <a download="question.xls" :href="questionUrl"></a>
        </div>
        <div class="notice">
            <p>1.</p>
            <p>2.</p>
        </div>
        </Col>
    </Row>
    <Row class="block-box">
        <Col span="24">
        <h3 style="margin:8px;"></h3>

        <div class="notice">
            <p>1.</p>
        </div>
        <Row>
            <Col class="set-box" span="12">
            <h3></h3>
            <div class="form">
                <Tree :data="treeData" ref="tree" @on-select-change="selectChange"></Tree>
            </div>
            </Col>
            <Col span="12">
            <p style="font-size:14px;margin-bottom:10px;"><b>{{selectName}}</b></p>
            <div style="margin-bottom:10px;">
                <Button type="primary" @click="downStuInfo"></Button>
                <div class="down-url" v-if="studentPrepared">
                    <a download="students.xls" :href="studentUrl"></a>
                </div>
            </div>
            <div style="margin-bottom:10px;">
                <Button type="primary" @click="downScoreInfo"></Button>
                <div class="down-url" v-if="scorePrepared">
                    <a download="scores.xls" :href="scoreUrl"></a>
                </div>
            </div>

            </Col>
        </Row>
        </Col>
    </Row>
</div>
</template>

<script>
import SubTitle from "../components/SubTitle";
export default {
    data() {
        return {
            selectName: "",
            selectNodeType: "none",
            selectNode: null,
            treeData: [],
            scorePrepared: false,
            questionPrepared: false,
            studentPrepared: false,
            selectCate: null,
            tableCategoryList: [],
            scoreUrl: "",
            questionUrl: "",
            studentUrl: "",
            file: null,
            headers: {
                "Content-Type": "multipart/form-data"
            }
        };
    },
    components: {
        SubTitle
    },
    beforeMount() {
        this.getQuesType();
        this.getDeptTree();
    },
    computed: {
        userType() {
            let userType =
                window.sessionStorage.getItem("userType") ||
                this.$store.getters.userType;
            return userType;
        }
    },
    methods: {
        getDeptTree() {
            this.$http.get("/admin/dept/getDeptNodeTree").then(res => {
                console.log("dept info", res.data);
                if (res.data.status == 200) {
                    let deptTree = res.data.data;
                    console.log("dept tree", deptTree);
                    let movedDeptTree = moveUnToLast(deptTree);

                    let tracedTreeArr = traceDeptTree(movedDeptTree);
                    this.formatTreeData(tracedTreeArr);
                } else {
                    this.$Message.error({
                        content: ""
                    });
                }
            });
        },
        formatTreeData(tracedTreeArr) {
            this.treeData = [];
            let root = {
                title: "",
                expand: true,
                nodeType: "school",
                deptName: "",
                children: tracedTreeArr
            };
            this.treeData.push(root);
        },
        selectChange() {
            let selected = this.$refs["tree"].getSelectedNodes();
            console.log(selected);
            if (selected.length > 0) {
                this.selectNode = selected[0];
                this.selectNodeType = selected[0].nodeType;
                this.selectName = this.selectNode.deptName;
            } else {
                this.selectNodeType = "none";
                this.selectNode = null;
            }
        },
        formatTableCateList(cateArr) {
            this.tableCategoryList = [];
            this.tableCategoryList.push({
                label: "",
                value: -1
            });
            cateArr.forEach(item => {
                let temp = {};
                temp.label = `${item.typeName}-${item.count}`;
                temp.value = item.typeId;
                this.tableCategoryList.push(temp);
            });
        },
        getQuesType() {
            this.$http.get("/admin/question/getQuestionType").then(res => {
                console.log("", res.data);
                if (res.data.status == 200) {
                    let cateArr = res.data.data;
                    this.formatTableCateList(cateArr);
                }
            });
        },
        downloadQues() {
            if (this.selectCate) {
                console.log("you select", this.selectCate);
                this.questionPrepared = false;
                this.$http
                    .get(
                        `/admin/question/exportQuestionToExcel?questionTypeId=${
                            this.selectCate
                        }`, {
                            responseType: "blob"
                        }
                    )
                    .then(res => {
                        if (res.status == 200) {
                            let data = res.data;
                            let blob = new Blob([data]);
                            this.questionUrl = URL.createObjectURL(blob);
                            this.$Message.success("excel");
                            this.questionPrepared = true;
                        }
                    });
            } else {
                this.$Message.error("");
            }
        },
        downScoreInfo() {
            let deptType, deptId;
            console.log(this.selectNodeType);
            switch (this.selectNodeType) {
                case "none":
                    this.$Message.error("");
                    return;

                case "school":
                    this.$Message.error("");
                    return;

                case "academy":
                    deptType = 1;
                    deptId = this.selectNode.deptId;
                    break;
                case "grade":
                    deptType = 3;
                    deptId = this.selectNode.deptId;
                    break;
                case "class":
                    deptType = 5;
                    deptId = this.selectNode.deptId;
                    break;
                default:
                    this.$Message.error("");
                    return;

            }
            this.scorePrepared = false;
            this.$http
                .post(
                    "/admin/examRule/exportReportToExcel", {
                        deptType,
                        deptId,
                        userType: 5
                    }, {
                        responseType: "blob"
                    }
                )
                .then(res => {
                    if (res.status == 200) {
                        let data = res.data;
                        let blob = new Blob([data]);
                        this.scoreUrl = URL.createObjectURL(blob);
                        this.scorePrepared = true;
                    }
                });
        },
        downStuInfo() {
            let deptType, deptId;
            console.log(this.selectNodeType);
            switch (this.selectNodeType) {
                case "none":
                    this.$Message.error("");
                    return;

                case "school":
                    this.$Message.error("");
                    return;

                case "academy":
                    deptType = 1;
                    deptId = this.selectNode.deptId;
                    break;
                case "grade":
                    deptType = 3;
                    deptId = this.selectNode.deptId;
                    break;
                case "class":
                    deptType = 5;
                    deptId = this.selectNode.deptId;
                    break;
                default:
                    this.$Message.error("");
                    return;
            }
            this.studentPrepared = false;
            this.$http
                .post(
                    "/admin/manage/exportUserToExcel", {
                        deptType,
                        deptId,
                        userType: 5
                    }, {
                        responseType: "blob"
                    }
                )
                .then(res => {
                    if (res.status == 200) {
                        let data = res.data;
                        let blob = new Blob([data]);
                        this.studentUrl = URL.createObjectURL(blob);
                        this.studentPrepared = true;
                    }
                });
        },
        fileFormatError() {
            this.$Message.error({
                content: ",Excel"
            });
        },
        beforeQuesUp(file) {
            this.file = file;
            let formData = new FormData();
            formData.append("upfile", this.file);
            let config = {
                headers: {
                    "Content-Type": "multipart/form-data"
                }
            };

            this.$http
                .post(
                    "/admin/question/importQuestionFromExcel",
                    formData,
                    config
                )
                .then(response => {
                    console.log(response)
                    if (response.status === 200) {
                        if (response.data.status == 200) {
                            this.$Message.success({
                                content: ""
                            });
                        } else {
                            let msg = response.data.msg
                            this.$Message.error({
                                content: msg
                            });
                        }
                    }
                });

            return false;
        },
        beforeStusUp(file) {
            this.file = file;
            let formData = new FormData();
            formData.append("upfile", this.file);
            let config = {
                headers: {
                    "Content-Type": "multipart/form-data"
                }
            };

            this.$http
                .post("/admin/manage/importUserFromExcel", formData, config)
                .then(response => {
                    if (response.status === 200) {
                        if (response.data.status == 200) {
                            this.$Message.success({
                                content: ""
                            });
                        } else {
                            let msg = response.data.msg
                            this.$Message.error({
                                content: msg
                            });
                        }
                    }
                });

            return false;
        }
    }
};

function moveUnToLast(treeArr) {
    let newTreeArr = treeArr.slice(0);
    let unIndex = newTreeArr.findIndex(item => {
        return item.deptType === 0;
    });
    newTreeArr.splice(unIndex, 1);
    return newTreeArr;
}

function traceDeptTree(treeArr) {
    let newTreeArr = treeArr.slice(0);
    newTreeArr.forEach(item => {
        item.title = item.deptName;
        item.deptId = item.deptId;

        switch (item.deptType) {
            case 1:
                item.nodeType = "academy";
                //item.expand = true;
                break;
            case 3:
                item.nodeType = "grade";
                break;
            case 5:
                item.nodeType = "class";
                item.title = `${item.deptName} =>${
                    item.studentNum ? item.studentNum : 0
                }`;
                break;
            default:
                item.nodeType = "none";
                break;
        }
        if (item.children) {
            traceDeptTree(item.children);
        } else {
            delete item.children;
        }
    });

    return newTreeArr;
}
</script>
