<style scoped>
.page-box {}

.table-box {
    padding: 5px 20px;
}

.btn-box {
    text-align: left;
    margin: 5px auto;
}

.input-item {
    margin-bottom: 10px;
}

.input-item-title {
    font-weight: bold;
    margin-bottom: 3px;
}

.modal-title {}

.modal-editor {
    margin-top: 10px;
}

.modal-preview {
    margin-top: 10px;
}

.preview-title {
    font-size: 1.1em;
    font-weight: bold;
    text-align: center;
}
</style>
<template>
<div>
    <sub-title title="" />
    <Alert style="text-align:left;" type="warning" closable></Alert>
    <div class="table-box">
        <Row>
            <Col class="btn-box" span="24">
            <ButtonGroup>
                <Button type="primary" @click="addNewRegu"></Button>
            </ButtonGroup>
            </Col>
        </Row>
        <Table @on-row-dblclick="clickRow" size="small" border :columns="columnsData" :data="tableData"></Table>

    </div>
    <Modal v-model="fullModal" fullscreen :title="modalInfo.modalTitle">
        <div>
          <Alert type="warning" closable>word</Alert>
            <Row>
                <Col class="modal-title" span="24">
                <Input v-model="modalInfo.reguTitle" placeholder=""></Input>

                </Col>
                <Col class="modal-editor" span="24">
                <div class="editor-box" id="editor" ref="editor" style="text-align:left"></div>
                </Col>
                <Col class="modal-preview" span="24">
                <div class="preview-title">
                    {{modalInfo.previewTitle}}
                </div>
                <div v-html="modalInfo.previewContent">

                </div>
                </Col>
            </Row>

        </div>
        <div slot="footer">

            <Button v-if="modalInfo.type>0" type="primary" @click="confirmModify"></Button>
            

            <Button v-if="modalInfo.type<1" type="primary" @click="confirmAdd"></Button>
            <Button type="warning" @click="preview"></Button>
        </div>
    </Modal>
    <Modal v-model="dbclickModal" :title="detailInfo.title" width="500">
        <div class="detail-modal">
            <div class="detail-content" v-html="detailInfo.content"></div>
        </div>
        <div slot="footer">
            <Button type="warning" @click="modifyRegu"></Button>
            <Button type="error" @click="deleteRegu"></Button>
        </div>
    </Modal>
</div>
</template>

<script>
import E from "wangeditor";
import SubTitle from "../components/SubTitle";
import {
    secondsToDateString
} from '../utils/dateTools.js'
export default {
    data() {
        return {
            dbclickModal: false,
            columnsData: [{
                    title: "",
                    key: "index"
                },
                {
                    title: "",
                    key: "name"
                }
            ],
            tableData: [],
            fullModal: false,
            modalInfo: {
                modalTitle:'',
                type:0, // 0   1 
                regulationId:0,  // 0 
                reguTitle: "",
                previewTitle: "",
                previewContent: ""
            },
            detailInfo: {
                regulationId:0,
                title: '',
                content: ''
            }
        };
    },
    async beforeMount() {
        this.getRegulations()
    },
    mounted() {
        var editor = new E(this.$refs.editor);
        editor.customConfig.onchange = html => {
            this.editorContent = html;
        };
        editor.customConfig.zIndex = 100;
        editor.customConfig.menus = [
            "head", // 
            "bold", // 
            "fontSize", // 
            "fontName", // 
            "italic", // 
            "underline", // 
            "strikeThrough", // 
            "foreColor", // 
            "backColor", // 
            "list", // 
            "justify", // 
            "undo", // 
            "redo" // 
        ];
        editor.create();
        this.editor = editor;
    },
    components: {
        SubTitle
    },
    methods: {
        getRegulations() {
            this.$http.get('/admin/regulation/findAllRegulationList').then(res => {
                console.log('get regulations', res.data)
                if (res.data.status == 200) {
                    let titleArr = res.data.data;
                    this.formatTableData(titleArr)
                }
            })
        },
        getReguById(id, cb) {
            this.$http.get(`/admin/regulation/findRegulationById?regulationId=${id}`).then(res => {
                console.log('get regulation by id', res.data)
                if (res.data.status == 200) {
                    cb(res.data.data)
                } else {
                    this.$Message.error('')
                }
            })
        },
        addNewRegu() {
            console.log("");
            this.editor.txt.clear()
            this.modalInfo.modalTitle = ""
            this.modalInfo.reguTitle = "";
            this.modalInfo.previewContent = "";
            this.modalInfo.previewTitle = "";
            this.modalInfo.type = 0;
            this.modalInfo.regulationId = 0
            this.fullModal = true;
        },

        clickRow(rowData) {
            console.log("rowData", rowData);
            let regulationId = rowData.regulationId;
            this.getReguById(regulationId, function (data) {
                this.detailInfo.title = data.regulationTitle;
                this.detailInfo.content = data.regulationText;
                this.detailInfo.regulationId = data.regulationId;
                this.dbclickModal = true
            }.bind(this))

        },
        refreshTable() {
            this.getRegulations()
        },

        formatTableData(dataArr) {
            this.tableData = [];
            dataArr.forEach(item => {
                let temp = {};
                temp.index = item.regulationId
                temp.regulationId = item.regulationId
                temp.name = item.regulationTitle
                this.tableData.push(temp);
            });
        },
        preview() {
            let html = this.editor.txt.html();
            this.modalInfo.previewTitle = this.modalInfo.reguTitle;
            this.modalInfo.previewContent = html;
            console.log("html", html);
        },
        confirmAdd() {
            let html = this.editor.txt.html();
            let title = this.modalInfo.reguTitle;
            if (title.length > 0 && html.length > 0) {
                this.$http
                    .post(`/admin/regulation/saveRegulation`, {
                        regulationTitle: title,
                        regulationText: html
                    })
                    .then(res => {
                        console.log("add new regulations", res.data);
                        if (res.data.status == 200) {
                            this.$Message.success('');
                            this.refreshTable()
                            this.fullModal = false
                        }
                    });
            }
        },
        modifyRegu(){
          this.modalInfo.type = 1;
          this.modalInfo.regulationId = this.detailInfo.regulationId;
          this.modalInfo.reguTitle = this.detailInfo.title
          this.modalInfo.previewContent = "";
          this.modalInfo.previewTitle = "";
          this.modalInfo.modalTitle = ""
          this.editor.txt.html(this.detailInfo.content)
          this.fullModal = true
        },
        deleteRegu(){
          let regulationId=this.detailInfo.regulationId
          this.$Modal.confirm({
            title:'',
            onOk:function(){
              console.log('')
              this.$http.post('/admin/regulation/deleteRegulations',{
                regulationIdListJson:JSON.stringify([regulationId])
              }).then(res=>{
                if (res.data.status == 200) {
                  this.$Message.success('')
                  this.refreshTable()
                  this.dbclickModal = false
                }
              })
            }.bind(this)
          })
        },
        confirmModify(){
          let html = this.editor.txt.html();
          let regulationId=this.detailInfo.regulationId
            let title = this.modalInfo.reguTitle;
            if (title.length > 0 && html.length > 0) {
                this.$http
                    .post(`/admin/regulation/updateRegulation`, {
                        regulationId,
                        regulationTitle: title,
                        regulationText: html
                    })
                    .then(res => {
                        console.log("add new regulations", res.data);
                        if (res.data.status == 200) {
                            this.$Message.success('');
                            this.refreshTable()
                            this.fullModal = false
                            this.dbclickModal = false
                        }
                    });
            }
        }
    }
};
</script>
