<style scoped>
.page-box {
}
.table-box {
  padding: 5px 20px;
}
.btn-box {
  text-align: left;
  margin: 5px auto;
}

.input-item {
  margin-bottom: 10px;
}
.input-item-title {
  font-weight: bold;
  margin-bottom: 3px;
}
</style>
<template>
 <div>
   <sub-title title="" />
   <div class="table-box">
     <Row>
        <Col class="btn-box" span="24">
          <ButtonGroup>
              <Button type="primary" @click="addNewUser"></Button>
          </ButtonGroup>
        </Col>
    </Row>
    <Table @on-row-dblclick="clickRow" size="small" border :columns="columnsData" :data="tableData"></Table>
     <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
          <Page :total="tableTotal" :page-size="30" :current="currentPage" @on-change="changePage"></Page>
        </div>
      </div>
   </div>

   <Modal @ v-model="detailModal" width="500">
        <p slot="header">
            <span></span>
        </p>
        <div>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title"></p>
                  <Input disabled  v-model="clickRecentUser.userName" placeholder="" style="width: 300px" />
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title"></p>
                  <Input :disabled="inputDis" v-model="clickRecentUser.realName" placeholder="" style="width: 300px" />
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title">//</p>
                  <Cascader :disabled="inputDis" style="width: 300px" :data="cascaderData" v-model="clickRecentUser.deptArr"></Cascader>
                </Col>
            </Row>
        </div>
        <div slot="footer">
            <Button type="info" @click="correctPwd"></Button>
            <Button type="error" :loading="delete_loading" @click="deleteRecent"></Button>
            <Button :type="crtBtnType" :loading="correct_loading" @click="correctRecent">{{crtBtnTitle}}</Button>
        </div>
    </Modal>

    <Modal @ v-model="addNewModal" width="500">
        <p slot="header">
            <span></span>
        </p>
        <div>
          <Alert closable></Alert>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title"></p>
                  <Input  v-model="newUserInfo.userName" placeholder="" style="width: 300px" />
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title"></p>
                  <Input v-model="newUserInfo.realName" placeholder="" style="width: 300px" />
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title">//</p>
                  <Cascader style="width: 300px" :data="cascaderData" v-model="newUserInfo.deptArr"></Cascader>
                </Col>
            </Row>

            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title"></p>
                  <Input type="password" v-model="newUserInfo.password" placeholder="" style="width: 300px" />
                </Col>
            </Row>
        </div>
        <div slot="footer">
            <Button type="error" :loading="delete_loading" @click="clearAddNew"></Button>
            <Button type="success" :loading="correct_loading" @click="confirmAddNew"></Button>
        </div>
    </Modal>
 </div>
</template>

<script>
import SubTitle from "../components/SubTitle";
export default {
  data() {
    return {
      tableTotal:0,
      currentPage:1,
      newPassword: "",
      inputDis: true,
      correct_loading: false,
      delete_loading: false,
      crtBtnType: "warning",
      crtBtnTitle: "",
      clickRecentUser: {
        userName: "",
        realName: "",
        userId: 0,
        deptArr: []
      },
      newUserInfo: {
        userName: "",
        realName: "",
        deptArr: [],
        password: "",
        dbpassword: ""
      },
      cascaderData: [],
      detailModal: false,
      addNewModal: false,
      columnsData: [
        {
          title: "",
          key: "username"
        },
        {
          title: "",
          key: "realname"
        },
        {
          title: "",
          key: "academy"
        },
        {
          title: "",
          key: "grade"
        },
        {
          title: "",
          key: "class"
        }
      ],
      tableData: []
    };
  },
  async beforeMount() {
    if (await this.getDeptInfo()) {
      this.getAdminUser(1);
    }
    this.getDeptTree();
  },
  components: {
    SubTitle
  },
  methods: {
    changePage(index){
      this.getAdminUser(index)
    },
    getDeptTree() {
      this.$http.get("/admin/dept/getDeptNodeTree").then(res => {
        console.log("dept info", res.data);
        if (res.data.status == 200) {
          let deptTree = res.data.data;
          console.log("dept tree", deptTree);
          this.cascaderData = traceDeptTree(deptTree);
        } else {
          this.$Message.error({
            content: ""
          });
        }
      });
    },
    getDeptInfo() {
      return new Promise((resolve, reject) => {
        this.deptMap = Object.create(null);
        this.$http.get("/admin/dept/getAllDept").then(res => {
          console.log("dept info", res.data);
          if (res.data.status == 200) {
            let deptArr = res.data.data;
            deptArr.forEach(item => {
              this.deptMap[item.deptId] = item.deptName;
            });
            resolve(true);
          } else {
            this.$Message.error({
              content: ""
            });
            reject(false);
          }
        });
      });
    },
    clickRow(rowData) {
      this.resetCrtBtn();
      this.clickRecentUser.userName = rowData.username;
      this.clickRecentUser.realName = rowData.realname;
      this.clickRecentUser.userId = rowData.userId;
      this.clickRecentUser.deptArr = [
        rowData.academyId,
        rowData.gradeId,
        rowData.classId
      ];
      console.log(this.clickRecentUser.deptArr);
      this.detailModal = true;
    },
    refreshTable(){
      this.getAdminUser(1)
    },
    getAdminUser(page) {
      this.$http.get(`/admin/manage/getAllTeachers?page=${page}&row=30`).then(res => {
        console.log("/admin/manage/getAllTeachers", res.data);
        if (res.data.status == 200) {
          this.tableTotal = res.data.data.count;
          this.formatTableData(res.data.data.userList);
        }
      });
    },
    formatTableData(dataArr) {
      this.tableData = [];
      dataArr.forEach(item => {
        let temp = {};
        temp.username = item.userName;
        temp.realname = item.realName;
        temp.academy = this.deptMap[item.academyId];
        temp.grade = this.deptMap[item.gradeId];
        temp.class = this.deptMap[item.classId];
        temp.academyId = item.academyId;
        temp.gradeId = item.gradeId;
        temp.classId = item.classId;
        temp.userId = item.userId;
        temp.reg = "";
        this.tableData.push(temp);
      });
    },
    addNewUser() {
      this.newUserInfo.userName = "";
      this.newUserInfo.realName = "";
      this.newUserInfo.deptArr = [];
      this.newUserInfo.password = "";
      this.newUserInfo.dbpassword = "";
      this.addNewModal = true;
    },
    clearAddNew() {
      this.newUserInfo = {
        userName: "",
        realName: "",
        deptArr: [],
        password: "",
        dbpassword: ""
      };
    },
    confirmAddNew() {
      const reg = /^[a-zA-Z][a-zA-Z0-9_]*$/gi;
      let { userName, realName,password } = this.newUserInfo;
      if (userName.length > 1 && userName.length < 20) {
        if (!reg.test(userName)) {
        
          this.$Message.error("");
          return;
        }
      } else {
        this.$Message.error("");
        return;
      }
      if (realName.length > 1 && realName.length < 20) {
        console.log('')
      } else {
        this.$Message.error("");
        return;
      }
      if (password.length > 5 && password.length < 20) {
        console.log('')
      } else {
        this.$Message.error("6");
        return;
      }
      let academyId, gradeId, classId;
      let deptArr = this.newUserInfo.deptArr;
      if (deptArr.length === 1 && this.deptMap[deptArr[0]] == "") {
        academyId = deptArr[0];
        gradeId = deptArr[0];
        classId = deptArr[0];
      }
      if (deptArr.length === 3) {
        academyId = deptArr[0];
        gradeId = deptArr[1];
        classId = deptArr[2];
      } else {
        this.$Message.error("");
        return;
      }

      this.$http
        .post("/admin/manage/saveUser", {
          userName: userName,
          realName: realName,
          userType: 3,
          classId,
          password,
          gradeId,
          academyId,
          sex: 0
        })
        .then(res => {
          console.log("/admin/manage/saveUser", res.data);
          if (res.data.status == 200) {
            this.$Message.success("");
            this.addNewModal = false;
            this.refreshTable();
          } else {
            this.$Message.error("");
          }
        });
    },
    correctPwd() {
      console.log("");

      this.$Modal.confirm({
        render: h => {
          return h("Input", {
            props: {
              value: this.newPassword,
              autofocus: true,
              placeholder: ""
            },
            on: {
              input: val => {
                this.newPassword = val;
              }
            }
          });
        },
        onCancel: () => {
          console.log("");

          this.newPassword = "";
        },
        onOk: async () => {
          console.log("");

          if (this.newPassword.length > 0) {
            console.log("");
            let { userName, realName, userId, deptArr } = this.clickRecentUser;
            this.$http
              .post("/admin/manage/updateTeacher", {
                userId,
                userType: 3,
                userName: userName,
                realName: realName,
                password: this.newPassword,
                academyId: deptArr[0],
                gradeId: deptArr[1],
                classId: deptArr[2]
              })
              .then(res => {
                console.log("update user info ", res.data);
                if (res.data.status == 200) {
                  this.$Message.success({
                    content: ""
                  });
                } else {
                  this.$Message.error({
                    contetn: ""
                  });
                }
              });
            this.newPassword = "";
          } else {
            this.$Message.error("");
          }
        }
      });
    },
    resetCrtBtn() {
      this.inputDis = true;
      this.crtBtnType = "warning";
      this.crtBtnTitle = "";
    },
    deleteRecent() {
      console.log("");
      this.$Modal.confirm({
        title: "",
        content: `<b>${this.clickRecentUser.userName}</b>`,
        onOk: () => {
          let userId = this.clickRecentUser.userId;
          let arr = [];
          arr.push(userId);
          this.$http
            .post("/admin/manage/deleteUsers", {
              userIdJson: JSON.stringify(arr)
            })
            .then(res => {
              if (res.data.status == 200) {
                this.$Message.success("");
                this.refreshTable();
                this.detailModal = false;
              } else {
                this.$Message.error("");
              }
            });
        }
      });
    },
    correctRecent() {
      if (this.inputDis) {
        console.log("");
        this.inputDis = false;
        this.crtBtnType = "success";
        this.crtBtnTitle = "";
      } else {
        console.log("");
        let { userName, realName, userId, deptArr } = this.clickRecentUser;
        this.$http
          .post("/admin/manage/updateTeacher", {
            userId,
            userType: 3,
            userName: userName,
            realName: realName,
            password: "123456",
            academyId: deptArr[0],
            gradeId: deptArr[1],
            classId: deptArr[2]
          })
          .then(res => {
            console.log("update user info ", res.data);
            if (res.data.status == 200) {
              this.$Message.success({
                content: ""
              });
              this.refreshTable();
              this.detailModal = false;
            } else {
              this.$Message.error({
                contetn: ""
              });
            }
          });
      }
    }
  }
};
function traceDeptTree(treeArr) {
  let newTreeArr = treeArr.slice(0);
  treeArr.forEach(item => {
    item.label = item.deptName;
    item.value = item.deptId;
    if (item.children) {
      traceDeptTree(item.children);
    } else {
      delete item.children;
    }
  });

  return newTreeArr;
}
</script>

