<style scoped>
.page-box {
  text-align: left;
}
 .table-box {
  padding:5px 20px;
}
.btn-box {
  text-align: left;
  margin: 5px auto;
}
</style>
<template>
 <div class="page-box">
    <sub-title title="" />
    <Alert type="warning" closable></Alert>
   <div class="table-box">
     <Row>
        <Col class="btn-box" span="24">
          <ButtonGroup>
              <Button type="primary" @click="addNewCate"></Button>
          </ButtonGroup>

         
        <Modal v-model="deleteModal" width="360">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>{{this.aimCateName}}</span>
            </p>
            <div style="text-align:center">
                <p>.</p>
                <p>?</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long :loading="deleteLoading" @click="confirmDel"></Button>
            </div>
        </Modal>
        </Col>
    </Row>
    <Table @on-row-dblclick="clickDel" size="small" border :columns="columnsData" :data="tableData"></Table>
   </div>
 </div>
</template>

<script>
import SubTitle from '../components/SubTitle'
 export default {
   data() {
     return {
       aimCateName:'',
       aimCateID:null,
       deleteLoading:false,
       newCate:'',
       deleteModal:false,
       columnsData:[
        {
          title: "",
          key: "index"
        },
        {
          title: "",
          key: "cateName"
        },
        {
          title: "ID",
          key: "cateID"
        },
        {
          title: "",
          key: "qNum"
        }
       ],
       tableData:[

       ]
     }
   },
   beforeMount(){
     this.getRecentCate()
   },
   components: {
     SubTitle
   },

   methods: {
     getRecentCate(){
       this.$http.get('/admin/question/getQuestionType').then(res=>{
         console.log('/admin/question/getQuestionType',res.data)
         if (res.data.status == 200) {
           let data= res.data.data;
            this.formatTable(data)
         }
       })
     },
     formatTable(sourceArr){
       this.tableData = []
       let index=1
       sourceArr.forEach(item=>{
         let temp={}
         temp.index = index;
          temp.cateName = item.typeName;
          temp.cateID = item.typeId;
          temp.qNum = item.count;

          this.tableData.push(temp)
         index++;
       })
     },
     addNewCate(){
       this.$Modal.confirm({
                    render: (h) => {
                        return h('Input', {
                            props: {
                                value: this.newCate,
                                autofocus: true,
                                placeholder: '...'
                            },
                            on: {
                                input: (val) => {
                                    this.newCate = val;
                                }
                            }
                        })
                    },
                    onOk:()=>{
                      console.log('')
                      this.$http.post('/admin/question/saveQuestionType',{
                        typeName:this.newCate
                      }).then(res=>{
                        console.log('/admin/question/saveQuestionType',res.data)
                        if (res.data.status ==200) {
                          this.$Message.success({
                            content:""
                          })
                          this.getRecentCate()
                          this.newCate = ''
                        }
                      })
                    },
                    onCancel:()=>{
                      console.log('')
                      this.newCate = ''
                    }
                })
     },
     closeDelModal(){
       this.aimCateName ='';
       this.aimCateID = null;
       this.deleteLoading = false;
       this.deleteModal = false
     },
     clickDel(data){
       console.log(data)
      if (data.qNum === 0) {
        this.aimCateName=data.cateName;
       this.aimCateID = data.cateID;
       this.deleteModal =true;
      } else {
        this.$Message.error('')
      }
     },
     confirmDel(){
      this.deleteLoading = true;

      this.$http.post(`/admin/question/deleteQuestionType?typeId=${this.aimCateID}`).then(res=>{
        console.log('/admin/question/deleteQuestionType',res.data)
        if (res.data.status == 200) {
          this.deleteLoading = false;
          this.$Message.success({
            content:''
          })
          this.getRecentCate()
          this.closeDelModal()
        }  else {
          this.deleteLoading = false;
          this.$Message.error({
            content:''
          })
        }
      })

     }
   }
}
</script>

